<template>
    <div id="app">
		<input v-model.number="num1">
		<input v-model.number="num2"> <br /><br />
		<button v-on:click="addcal">덧샘계산</button>
		<button v-on:click="subcal">뺄샘계산</button>
		<button v-on:click="multical">곱샘계산</button>
		<button v-on:click="divcal">나눗샘계산</button>
		<p>결과는 {{ result }} 입니다.</p>
	</div>
</template>

<script>
    export default{
        data(){
            return{
                num1:0,
				num2:0,
				result:0
            }
        },
        methods : {
            addcal:function() {
				this.result = this.num1 +  this.num2;
			},
			subcal:function() {
				this.result = this.num1 -  this.num2;
			},
			multical:function() {
				this.result = this.num1 *  this.num2;
			},
			divcal:function() {
				this.result = this.num1 /  this.num2;
			},
		}
    }
</script>

<style>
html {
	width: 80%;
	margin-right: auto;
	margin-left: auto;
	font-family: sans-serif;
	font-size: 16px;
	line-height: 1.5;
}

input,button,select,optgroup,textarea {
	font-family: inherit;
	font-size: inherit;
	line-height: inherit;
}

button {
	cursor: pointer;
	padding: 6px 12px;
	border-radius: 6px;
	color: #fff;
	border: 2px;
	background-color: #007bff;
	transition: background-color .2s
}
button:hover {
	background-color: #0069d9;
}
button:active {
	background-color: #003c7c;
}
button:disabled {
	opacity: .5;
	pointer-events: none;
}

input {
	padding: 6px 12px;
	border-radius: 6px;
	color: #495057;
	border: 2px solid #ced4da;
}

textarea {
	width: 500px;
	height: 200px;
}
select {
	border: 2px solid #ced4da;
}

</style>